<template>
  <div>
    <!-- <indexHeader></indexHeader> -->
    <div class="new-coins">
        <div class="new-coins-header flex center alcenter">
            <div class="tc white">
                <h3 style="font-size:30px">{{$t('comion.commission')}}</h3>
            </div>
        </div>
        <div class="mt30 new-coins-btn white pdtb20 pdlr20 ft12">
            <div >
                <p class="ptb5">{{$t('comion.link')}}</p>
                <div class="mt15 flex alcenter between">
                    <div class="flex ptb10 bd3" style="width:60%">
                        <p class="bdr3 flex1 plr10">{{extension_code}}</p>
                        <p class="tc" style="width:150px" id="copy1" @click="copy1">{{$t('comion.copy')}}</p>
                    </div>
                    <div class="flex ptb10 bd3" style="width:35%">
                        <p class="bdr3 flex1 plr10 tc">{{$t('register.inviteCode')}}：{{code}}</p>
                        <p class="tc" style="width:100px"  id="copy2" @click="copy2">{{$t('asset.copy')}}</p>
                    </div>
                </div>
            </div>
            <div class="mt20 flex">
                <p>{{$t('comion.today')}}：<span class="blue">{{today_all_num}}</span> </p>
                <p class="ml20">{{$t('comion.total')}}：<span class="blue">{{all_num}}</span> </p>
            </div>
            <p class="ptb5 mt15">{{$t('comion.log')}}</p>
            <p class="flex alcenter tc ptb10 bdb">
                <span class="flex1">{{$t('td.time')}}</span>
                <span class="flex1">{{$t('td.num')}}</span>
            </p>
            <div class="scroll list-content tc fColor1">
                <ul>
                    <li class="flex alcenter tc ptb10 bdb" v-for="item in list">
                        <span class="flex1">{{item.created_time}}</span>
                        <span class="flex1">{{item.value}} {{item.currency_name}}</span>
                    </li>
                </ul>
                <p class="tc more ptb10" @click="getMore" v-if='more'>{{$t('td.more')}}</p>
                <p class="tc more ptb10" v-else>{{$t('td.nomore')}}</p>
            </div>
            <div>
                <p class="ptb10">{{$t('comion.rule')}}</p>
                <div style="line-height:1.5" v-html="content"></div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import "@/lib/clipboard.min.js"
// import indexHeader from "@/view/indexHeader";
// import indexFooter from "@/view/indexFooter";
export default {
    // components: { indexHeader, indexFooter },
    data() {
        return {
            content:'',
            code:'',
            extension_code:'',
            list:[],
            page:1,
            today_all_num:0,
            all_num:0,
            more:true
        };
    },
    created() {

    },
    mounted(){
        this.userInfo();
        this.getDetail();
        this.getLog();
    },
    methods: {
        copy1(){
            var that=this;
            var clipboard = new Clipboard('#copy1',{
                text:function(){
                    return that.extension_code;
                }
            });
            clipboard.on("success", function (e) {
                that.flags = true;
                layer.msg(that.$t('set.copysuccess'));       
            });
            clipboard.on("error", function (e) {
                that.flags = false;
                layer.msg(that.$t('set.recopy'))
            });
        },
         copy2(){
            var that=this;
            var clipboard = new Clipboard('#copy2',{
                text:function(){
                    return that.code;
                }
            });
            clipboard.on("success", function (e) {
                that.flags = true;
                layer.msg(that.$t('set.copysuccess'));       
            });
            clipboard.on("error", function (e) {
                that.flags = false;
                layer.msg(that.$t('set.recopy'))
            });
        },
        userInfo(){
            let that = this;
            this.$http({
                url: '/api/'+'user/info',
                method:'get',
                data:{},  
                headers: {'Authorization':  localStorage.getItem('token')},    
            }).then(res=>{
                if(res.data.type == 'ok'){
                    that.code=res.data.message.extension_code;
                    var loc= location.origin;
                    that.extension_code = loc+'/dist/#/components/register?extension_code='+that.code;
                }
                }).catch(error=>{
                    
            })               
        },
        getDetail(){
            this.$http({
                url: '/api/' + 'news/detail',
                method:'post',
                data:{id:158},
                headers: {'Authorization':  localStorage.getItem('token')},
            }).then(res=>{
                res = res.data;
                if(res.type  === 'ok'){
                    this.content=res.message.content;
                }else{
                    // layer.msg(res.message);
                }
            }).catch(error=>{
                console.log(error)
            })
        },
        getLog(){
            let that = this;
            this.$http({
                url: '/api/'+'user/transtate_history?page='+that.page,
                method:'get', 
                headers: {'Authorization':  localStorage.getItem('token')},    
            }).then(res=>{
                if(res.data.type == 'ok'){
                    let data = res.data.message.data;
                    let datas = data.list;
                    if(datas.length==res.data.message.limit){
                        that.more=true
                    }else{
                        that.more=false
                    }
                    that.all_num = data.all_num;
                    that.today_all_num = data.today_all_num;
                    that.list = that.list.concat(datas);
                //    console.log(res.data)
                }
                }).catch(error=>{
                    
            })               
        },
        getMore(){
            this.page++;
            this.getLog();
        }
    }
};
</script>

<style scoped>
.new-coins {
  min-width: 1200px;
  width: 100%;
  min-height: 600px;
}
.new-coins-header {
  width: 100%;
  height: 320px;
  background: url(../../static/imgs/app_banner3.jpg) no-repeat center;
}
.new-coins-header img {
  width: 100%;
  height: 320px;
}
.new-coins-btn {
  background-color: #22252b;
  width: 1200px;
  margin: 30px auto;
  border-radius: 4px;
}
.list-content{
    height: 400px;
    overflow-y: scroll;
}
.bor {
  border: 1px solid #aaa;
}
</style>



